:root {
  --primary-color: #6c63ff;
}

body {
  margin: 0;
  padding: 0;
}

*, :after, :before {
  box-sizing: border-box;
}

.main {
  grid-gap: .4rem;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  place-items: center;
  display: grid;
}

.main .modal {
  z-index: 100;
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: transform .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(0);
}

.main .modal .redo {
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: 0;
  font-size: 2.4rem;
  position: absolute;
  top: 20px;
  left: 15px;
}

.main .modal .frame {
  z-index: 10;
  background: #fff;
  justify-content: center;
  align-items: center;
  width: 95%;
  max-width: 44rem;
  max-height: 90vh;
  padding: 2rem;
  display: flex;
  position: fixed;
}

.main .modal .frame * {
  max-height: 80vh;
}

.main .picker {
  z-index: 1;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 20px;
  right: 20px;
}

.main .picker .label {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: .47rem;
  font-weight: bold;
}

.main .picker .color {
  cursor: pointer;
  border: 0;
  width: 60px;
  height: 30px;
}

.main .anime {
  border: 1px solid var(--primary-color);
  cursor: pointer;
  background-color: #0000;
  justify-content: center;
  align-self: stretch;
  align-items: center;
  width: 100%;
  max-width: 31.5rem;
  padding: 1.5rem;
  transition: transform .3s;
  display: flex;
  position: relative;
}

.main .anime:focus, .main .anime:hover {
  transform: scale(.95);
}

.main .anime .animation {
  pointer-events: none;
  width: 100%;
}

.footer {
  letter-spacing: .04rem;
  text-align: center;
  width: 100%;
  padding: 50px 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

